"use client";

import { useState } from "react";

export default function Record() {
  const Emotions = ["happy", "sad", "angry", "excited", "anxious"];
  const [selectedEmotion, setSelectedEmotion] = useState<string>("happy");
  const [note, setNote] = useState<string>("");
  const DEV_USER_ID = 1;

  async function handleMomentSubmit() {
    const res = await fetch("http://localhost:3001/api/moments", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        user_id: DEV_USER_ID,
        emotion: selectedEmotion,
        note: note.trim() ? note.trim() : null,
        image_key: null,
      }),
    });

    console.log("status:", res.status);
    const data = await res.json();
    console.log("response data:", data);
  }

  return (
    <main style={{ padding: 24 }}>
      <h1>Witnessing the Now</h1>
      <h2>Current Resonance: {selectedEmotion}</h2>

      {/*  Select emotion */}
      <div>
        {Emotions.map((emo) => (
          <button
            key={emo}
            onClick={() => setSelectedEmotion(emo)}
            className={
              selectedEmotion === emo
                ? "bg-blue-500 text-white"
                : "bg-gray-200 text-gray-800"
            }
          >
            {emo}
          </button>
        ))}
      </div>

      {/* Note section - optional */}
      <div style={{ marginTop: 24 }}>
        <h2>Ephemeral Note</h2>
        Note (optional)
        <br />
        <textarea
          value={note}
          onChange={(e) => setNote(e.target.value)}
          placeholder="Write a short note...(optional)"
          rows={4}
          style={{ width: "100%", marginTop: 8 }}
        />
      </div>
      <p>Visual Fragments</p>
      <button onClick={handleMomentSubmit}>Keep as moment</button>
    </main>
  );
}
